<template>
  <div class="result_container">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="getInfo"
      offset="50"
    >
      <van-cell
        v-for="item in list"
        :key="item.art_id"
        :title="item.title"
        :to="`/article/` + item.art_id"
      />
    </van-list>
  </div>
</template>

<script>
import { getSearchInfo } from '@/api/search.js';

export default {
  name: 'Result',
  props: {
    searchText: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1,
      per_page: 15,
    };
  },
  methods: {
    async getInfo() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      try {
        const { data } = await getSearchInfo({
          page: this.page,
          per_page: this.per_page,
          q: this.searchText,
        });
        const { total_count, results } = data.data;

        this.list.push(...results);
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        if (results.length) {
          this.page++;
        } else {
          this.finished = true;
        }
      } catch (e) {
        console.log(e);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.result_container {
  position: fixed;
  left: 0;
  right: 0;
  top: 54px;
  bottom: 0;
  overflow-y: auto;
}
</style>
